
import { defineComponent, watch } from 'vue'
import { Notification } from "@/composables/notifications"
import { 
    IonItem,
    IonList,
    IonLabel,
    IonChip
} from "@ionic/vue";
export default defineComponent({
    components: {
        IonLabel,
        IonItem,
        IonList,
        IonChip
    },
    setup() {
        const { 
            openNotification,
            markAllAsRead,
            sortedNotifications,
            notificationData,
            hasNotifications
        } = Notification()
        watch(notificationData, (d) => {
            if (d) {
                markAllAsRead()
            }
        },{ deep : true, immediate: true})
        return {
            openNotification,
            sortedNotifications,
            hasNotifications,
        }
    }
})
